body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #afe0ce;
}
.set {
  height: 30.875em;
  margin: 0 auto;

  position: relative;
  border: 1px solid #160801;
  border-radius: 1em;
  background: #ff5964;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.5) inset,
    0 1px rgba(212, 152, 125, 0.2) inset, 0 5px 15px rgba(0, 0, 0, 0.5);
}
li {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  float: left;
}

ul .white {
  height: 16em;
  width: 4em;
  z-index: 1;
  border-left: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
  border-radius: 0 0 5px 5px;
  box-shadow: -1px 0 0 rgba(255, 255, 255, 0.8) inset, 0 0 5px #ccc inset,
    0 0 3px rgba(0, 0, 0, 0.2);
  background: -webkit-linear-gradient(top, #eee 0%, #fff 100%);
  background: linear-gradient(top, #eee 0%, #fff 100%);
  -webkit-transition: all 0.07s ease;
  transition: all 0.07s ease;
}

ul .active {
  border-top: 1px solid #777;
  border-left: 1px solid #999;
  border-bottom: 1px solid #999;
  box-shadow: 2px 0 3px rgba(0, 0, 0, 0.1) inset,
    -5px 5px 20px rgba(0, 0, 0, 0.2) inset, 0 0 3px rgba(0, 0, 0, 0.2);
  background: -webkit-linear-gradient(top, #fff 0%, #e9e9e9 100%);
  background: linear-gradient(top, #fff 0%, #e9e9e9 100%);
  -webkit-transition: all 0.07s ease;
  transition: all 0.07s ease;
}

.black {
  height: 8em;
  width: 2em;
  margin: 0 0 0 -1em;
  z-index: 2;
  border: 1px solid #000;
  border-radius: 0 0 3px 3px;
  box-shadow: -1px -1px 2px rgba(255, 255, 255, 0.2) inset,
    0 -5px 2px 3px rgba(0, 0, 0, 0.6) inset, 0 2px 4px rgba(0, 0, 0, 0.5);
  background: -webkit-linear-gradient(45deg, #222 0%, #555 100%);
  background: linear-gradient(45deg, #222 0%, #555 100%);
}

.black.active {
  box-shadow: -1px -1px 2px rgba(255, 255, 255, 0.2) inset,
    0 -2px 2px 3px rgba(0, 0, 0, 0.6) inset, 0 1px 2px rgba(0, 0, 0, 0.5);
  background: -webkit-linear-gradient(left, #444 0%, #222 100%);
  background: linear-gradient(left, #444 0%, #222 100%);
}

.a,
.g,
.f,
.d,
.c {
  margin: 0 0 0 -1em;
}

ul li:first-child {
  border-radius: 5px 0 5px 5px;
}
ul li:last-child {
  border-radius: 0 5px 5px 5px;
}

/*MEDIA QUERIES*/
@media (max-width: 420px) {
}
@media screen and (orientation: portrait) {
  .synth-logo,
  .effect-line,
  .keys {
    display: none !important;
  }
}
@media (min-width: 520px) {
  .flip-me-boo {
    display: none;
  }
}

@media screen and (orientation: landscape) {
  .flip-me-boo {
    display: none;
  }
  .synth-logo {
    display: none !important;
  }
  .set {
    font-size: 12px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.skrrt-animation {
  padding: 10px;

  max-width: 400px;
  width: 100%;

  z-index: 50;
}
.esizer {
  z-index: 10;
}
.synth-logo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
@-webkit-keyframes mymove {
  0% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  25% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  50% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  75% {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
  }
  100% {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
  }
}
@keyframes mymove {
  0% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  25% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  50% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  75% {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
  }
  100% {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
  }
}

.popUp {
  -webkit-animation-name: mymove;
          animation-name: mymove;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

/*EFFECT COMPONENTS*/
.range-slider {
  margin-top: 5px;
}
.first-effect-line {
  display: -webkit-flex;
  display: flex;
}
.effect-component {
  min-width: 200px;
  border: 2px solid pink;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
  background: white;
  box-shadow: 10px 20px 20px gray;
}
.effect-property-name {
  font-size: 12px;
  margin-bottom: 5px;
}
.effect-number-display {
  font-size: 12px;
}
.slider-with-text {
  margin-bottom: 3px;
}

